.route {
    color: black;
    cursor: move;
    border: 1px solid orange;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    margin-bottom: 5px;
    -webkit-animation: newElemShake 0.1s  ease-in;
            animation: newElemShake 0.1s  ease-in;
}
.route button {
    cursor: pointer;
    padding: 2px 5px 2px 5px;
    background: none;
    border: 1px solid orange;
    cursor: pointer;
    color: orange;
}
.route button:hover {
    background: orange;
    color: black;
}
li {
    list-style-type: none;
}


.route-builder {
    display: flex;
}
.route-builder__menu {
    margin-right: 20px;
}
.route-builder__form {
    margin-bottom: 10px;
}
.route-builder__form input {
    border: 1px solid black;
}
.route-builder__form input, .route-builder__form button {
    font-size: 15px;
    padding: 5px;
}
.route-builder__form button {
    margin-left: 5px;
    border: 1px solid orange;
    cursor: pointer;
    color: orange;
    background: none;

}
.route-builder__form button:hover {
    background: orange;
    color: black;
}

.yandex-map {
    position: relative;
    width: 600px;
    height: 600px;
    
}
.yandex-map__center {
    position: absolute;
    top: 50%;
    right: 50%;
    z-index: 9999;
    -webkit-animation: pulse 1.3s ease infinite;
            animation: pulse 1.3s ease infinite;

}
.top {
    position: absolute;
    height: 7px;
    border: 1px solid orange;
    top: -14px;
    right: -2px;
}
.bottom {
    position: absolute;
    height: 7px;
    border: 1px solid orange;
    top: 0px;
    right: -2px;
}
.right {
    position: absolute;
    width: 7px;
    border: 1px solid orange;
    top: -3px;
    right: -12px;
}
.left {
    position: absolute;
    width: 7px;
    border: 1px solid orange;
    top: -3px;
    right: 2px;
}
.yandex-map__text-center {
    display: flex;
    justify-content: center;
    color: orange;
    background: oldlace;
}

@media screen and (max-width: 1024px) {
    .yandex-map {
        width: 400px;
        height: 400px;  
    }
    .yandex-map__text-center {
        font-size: 14px;
    }
      
}
@media screen and (max-width: 720px) {
    .route-builder {
        flex-direction: column;
    }
    .route-builder__menu {
        margin-bottom: 10px;
        margin-right: 0px;
    }
    .route-builder__form {
        display: flex;
        justify-content: space-between;
    }
    
    .route-builder__form input {
        font-size: 12px;
        width: 235px;
    }

    .route-builder__form button {
        font-size: 12px;
    }
    .yandex-map {
        width: 320px;
        height: 320px;  
    }   
    .yandex-map__text-center {
        font-size: 11px;
    }  
}

@media screen and (max-width: 360px) { 
    .route-builder__form input {
        width: 215px;
    }
    .yandex-map {
        width: 300px;
        height: 300px;  
    }   
 
}
* {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    padding: 0;
    outline: 0;
}
::-moz-focus-outer, ::-moz-focus-inner {
    border: 0;
    padding: 0;
}
.content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.content__title {
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.content__title img:hover {
    -webkit-animation: shake 1s ease;
            animation: shake 1s ease;
}
.content__logo {
    width: 40px;
    height: 40px;
}
@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8); 
    }
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1); 
    }
    100% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}
@keyframes pulse {
    0% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8); 
    }
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1); 
    }
    100% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}

@-webkit-keyframes shake {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1); 
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2); 
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes shake {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1); 
    }
    50% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2); 
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@-webkit-keyframes newElemShake {
    0% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes newElemShake {
    0% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@media screen and (max-width: 1024px) {
    * {
        font-size: 14px;
    }
    .content__title h1 {
        font-size: 18px;
    }
    .content__logo {
        width: 30px;
        height: 30px;
    }
      
}
@media screen and (max-width: 720px) {
    * {
        font-size: 12px;
    }
    .content__title h1 {
        font-size: 16px;
    }    
}
